$font-stack:    Helvetica, sans-serif;
$black: #000;
$white: #fff;
$dark-green: #6b9775;
$light-green: #d0e2c1;
$grey: #A1A1A1;
$dark-grey: #696969;


*{
    margin: 0;
    padding: 0;
}

body {
    width: 100vw;
    height: 100vh;
    background: $light-green;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.mainContent{
    margin-left: auto;
    margin-right: auto;
    width: 1080px;
    height: 720px;
    background: $white;
    border-radius: 20px;
}


.logo{
    background: $dark-green;
    width: 400;
    height: 400;
    border-radius: 50%;
    margin-top: 200px;
}